/**
 * 
 */
export interface Agent {
  id: string;
  name: string;
  description: string;
  avatar?: string;
  model: string;
  status: AgentStatus;
  capabilities: string[];
  provider: string;
}

/**
 * 
 */
export type AgentStatus = 'active' | 'inactive' | 'error' | 'loading';

/**
 *  huihua.md 
 */
export type InteractiveData =
  | { type: 'userSelect'; params: { description?: string; userSelectOptions: { key: string; value: string }[] } }
  | { type: 'userInput'; params: { description?: string; inputForm: any[] } };

export interface ChatMessage {
  AI?: string;    // AI
  HUMAN?: string; // 
  id?: string;    // IDFastGPT responseChatItemId/
  feedback?: 'good' | 'bad' | null; // /good=bad=null=
  interactive?: InteractiveData; // FastGPT  detail=true
}

/**
 * 
 */
export interface OriginalChatMessage {
  id: string;
  role: 'user' | 'assistant' | 'system';
  content: string;
  timestamp: Date;
  metadata?: {
    model?: string;
    tokens?: number;
    provider?: string;
    agentId?: string;
  };
}

/**
 * 
 */
export interface ChatOptions {
  stream?: boolean;
  chatId?: string;
  detail?: boolean;
  temperature?: number;
  maxTokens?: number;
  // FastGPT 
  variables?: Record<string, any>; //  [key]
  responseChatItemId?: string;     //  IDFastGPT  ID 
}

/**
 * 
 */
export interface ChatResponse {
  id: string;
  object: string;
  created: number;
  model: string;
  choices: Array<{
    index: number;
    message: OriginalChatMessage;  // 
    finish_reason: string;
  }>;
  usage?: {
    prompt_tokens: number;
    completion_tokens: number;
    total_tokens: number;
  };
}

/**
 * 
 */
export interface StreamStatus {
  type: 'flowNodeStatus' | 'progress' | 'error' | 'complete';
  status: 'running' | 'completed' | 'error';
  moduleName?: string;
  progress?: number;
  error?: string;
}

/**
 * API
 */
export interface ApiError {
  code: string;
  message: string;
  details?: any;
  timestamp: string;
}

/**
 * 
 */
export type ThemeMode = 'light' | 'dark' | 'auto';

/**
 * 
 */
export interface ThemeConfig {
  mode: ThemeMode;
  isAutoMode: boolean;
  userPreference: ThemeMode;
}

/**
 * 
 */
export interface UserPreferences {
  theme: ThemeConfig;
  streamingEnabled: boolean;
  autoThemeSchedule: {
    enabled: boolean;
    lightModeStart: string; // HH:mm 
    darkModeStart: string;  // HH:mm 
  };
  defaultAgent?: string;
  language: 'zh-CN' | 'en-US';
}

/**
 *  huihua.md 
 */
export interface ChatSession {
  id: string;              // (id)
  title: string;           // 30
  agentId: string;         // ID
  messages: ChatMessage[]; //  [{'AI': string, 'HUMAN': string}]
  createdAt: Date;         // 
  updatedAt: Date;         // 
}

/**
 *  huihua.md 
 */
export interface AgentSessionsMap {
  [agentId: string]: ChatSession[];  // agentId:[]
}

/**
 *  huihua.md 
 */
export interface AppState {
  // 
  agents: Agent[];
  currentAgent: Agent | null;
  agentsLoading: boolean;
  agentsError: string | null;
  
  //  huihua.md 
  agentSessions: AgentSessionsMap;     // 
  currentSession: ChatSession | null;  // 
  messages: ChatMessage[];             // 
  isStreaming: boolean;
  streamingStatus: StreamStatus | null;
  
  // 
  theme: ThemeConfig;
  
  // 
  preferences: UserPreferences;
  
  // UI
  sidebarOpen: boolean;
  agentSelectorOpen: boolean;
  
  // 
  globalError: string | null;
}

/**
 * API
 */
export interface RequestOptions {
  timeout?: number;
  signal?: AbortSignal;
  retries?: number;
}

/**
 * 
 */
export interface AgentHealthStatus {
  agentId: string;
  status: AgentStatus;
  responseTime?: number;
  lastChecked: string;
  error?: string;
}

/**
 * Props
 */
export interface BaseComponentProps {
  className?: string;
  children?: React.ReactNode;
}

/**
 * Props
 */
export interface AgentSelectorProps extends BaseComponentProps {
  agents: Agent[];
  currentAgent: Agent | null;
  onAgentChange: (agent: Agent) => void;
  loading?: boolean;
  disabled?: boolean;
}

/**
 * Props
 */
export interface ThemeToggleProps extends BaseComponentProps {
  size?: 'sm' | 'md' | 'lg';
  showLabel?: boolean;
  variant?: 'icon' | 'button';
}

/**
 * Props
 */
export interface MessageProps extends BaseComponentProps {
  message: ChatMessage;  //  huihua.md 
  isStreaming?: boolean;
  onRetry?: () => void;
  onEdit?: (content: string) => void;
  onDelete?: () => void;
}

/**
 * Props
 */
export interface ChatInputProps extends BaseComponentProps {
  onSendMessage: (content: string, options?: ChatOptions) => void;
  disabled?: boolean;
  placeholder?: string;
  multiline?: boolean;
}

/**
 * 
 */
export const convertToHuihuaFormat = (messages: OriginalChatMessage[]): ChatMessage[] => {
  const result: ChatMessage[] = [];
  let currentPair: ChatMessage = {};
  
  messages.forEach(msg => {
    if (msg.role === 'user') {
      if (Object.keys(currentPair).length > 0) {
        result.push(currentPair);
        currentPair = {};
      }
      currentPair.HUMAN = msg.content;
    } else if (msg.role === 'assistant') {
      currentPair.AI = msg.content;
    }
  });
  
  if (Object.keys(currentPair).length > 0) {
    result.push(currentPair);
  }
  
  return result;
};

/**
 *  huihua.md 
 */
export const convertFromHuihuaFormat = (huihuaMessages: ChatMessage[]): OriginalChatMessage[] => {
  const result: OriginalChatMessage[] = [];
  
  huihuaMessages.forEach((msg, index) => {
    if (msg.HUMAN) {
      result.push({
        id: `${Date.now()}-${index}-user`,
        role: 'user',
        content: msg.HUMAN,
        timestamp: new Date()
      });
    }
    if (msg.AI) {
      result.push({
        id: `${Date.now()}-${index}-assistant`,
        role: 'assistant',
        content: msg.AI,
        timestamp: new Date()
      });
    }
  });
  
  return result;
};